import { Component } from '@angular/core';

import {IonicPage, Platform} from 'ionic-angular';
import {HttpClient} from "@angular/common/http";
import {HTTP} from "@ionic-native/http";
import {OrgInfoProvider} from "../../../providers/org-info/org-provider";

interface UserResponse {
    history: string;
    login: string;
    bio: string;
    company: string;
}

@IonicPage()
@Component({
  selector: 'segments-page',
  templateUrl: 'template.html'
})

export class AbouPage {
  home: string = "about";
  private about: any = {};

  constructor(private  http: HttpClient,
                        private  aboutdetail: OrgInfoProvider) {
this.loadData()

  }


    async loadData(){

        this.aboutdetail.query().then((res: any) => {

            this.about= JSON.parse(res.data)




        }).catch(er=> console.log(er))
        ;
    }



}



